@import '~antd/dist/antd.min.css';
.logo {
  height: 64px;
  position: relative;
  line-height: 64px;
  transition: all 0.3s;
  background: #002140;
  overflow: hidden;
  text-align: center;
}

.logo img {
  display: inline-block;
  vertical-align: middle;
  height: 32px;
}

.logo h1 {
  color: white;
  display: inline-block;
  vertical-align: middle;
  font-size: 20px;
  margin: 0 0 0 12px;
  font-family: 'Myriad Pro', 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-weight: 600;
}


.sider {
  min-height: 100vh;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
  position: relative;
  z-index: 10;
}
.ligth {
  background-color: white;
}
.ligth .logo {
  background: white;
}
.ligth .logo h1 {
  color: #002140;
}
